.search-box {
  position: relative;
  height: 28px;
  align-items: center;
  padding: 4px 8px;
  border-radius: 4px;
  display: flex;
  background: #f2f2f2;
  margin: 8px 12px;
}
input {
  height: 100%;
  width: 100%;
  background: #f2f2f2;
  border: none;
  outline: none;
}
img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  margin-right: 4px;
}
