  width: 24px;
  height: 24px;
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  display: inline-block;

  &-minus {
    background-image: url("./image/icon-minus.png");
  }

  &-add {
    background-image: url("./image/icon-add.png");
  }

  &-drag {
    background-image: url("./image/icon-drag.png");
  }
}

.card {
  background: #fff;
  overflow: auto;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  > .title {
    display: flex;
    overflow: hidden;
    height: 48px;
    line-height: 48px;
    font-size: 16px;
    padding: 0 12px;
    position: relative;
    color: #212121;
    background-repeat: no-repeat;
    border-bottom: 1px solid #e5e5e5;
  }

  .list {
    padding-left: 12px;

    &-item {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #e5e5e5;
      padding: 12px 12px 12px 0;
      background: #fff;
      overflow: auto;

      .cc-icon {
        flex-grow: 0;
        flex-shrink: 0;
      }

      .title {
        flex: 1;
        margin-left: 12px;
        color: #212121;
        line-height: 24px;
      }
    }
  }
}

.no-data {
  padding: 12px 20px;
  text-align: center;
  font-size: 12px;
  color: #999;
}

.app {
  background: #f2f2f2;
  height: 100%;
  margin: 0;
  padding: 0;
}

.bottomBar {
  display: flex;
  height: 49px;
  justify-content: space-between;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  margin-top: 12px;
  background: #ffffff;
}

.el-button {
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  margin: 6px;
  font-weight: bold;
  color: #212121;
  display: flex;
  width: 100%;
}

.primary {
  background-color: #276ff5;
  color: #FFFFFF;
}


